import Cookies from 'js-cookie'
import PaceHeader from './components/header'
import PaceList from './components/list'
export default {
  name: 'index',
  data () {
    return {
      projectList: [],
      tag: this.Run
    }
  },
  components: {
    PaceHeader,
    PaceList
  },
  methods: {
    judge () {
      if (!Cookies.get('id')) {
        this.$router.push('/loginError')
      }
    }
  },
  created () {
    this.judge()
  }
}



